button {
  padding: 20px 40px;
  font-size: 25px;
}
.bg {
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  background: #000;
  opacity: 0.6;
}
.popup {
  width: 400px;
  height: 500px;
  background: beige; 
  position: absolute;
  left: 50%;
  top: 50%; 
  padding: 30px;
  box-sizing: border-box;
  transform: translate(-50%, -50%); 
}
.popup .closex {
  position: absolute;
  right: 20px;
  bottom: 0px;
  cursor: pointer;
  border: 1px solid #000;
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  font-size: 40px;
}
.popup h2 {
  font-size: 50px;
}